export default {
    data() {
        return {
            iphoneX: (window.innerWidth == 375 && window.innerHeight == 812) ? true:false
        }
    },
    methods: {
        back() {
            this.$router.back();
        },
        jumpN(url) {
            console.log(url)
            if (!window.cordova) return;
            cordova.exec(function() {}, function() {}, 'nativeJs', 'jump', [{ url }]);
        },
        closeN() {
            if (!window.cordova) return;
            cordova.exec(function() {}, function() {}, 'nativeJs', 'view', [{ name: 'close' }]);
        },
        goLogin(){
            if (!window.cordova) return;
            cordova.exec(function() {}, function() {}, 'nativeJs', 'view', [{ name: 'goLogin' }]);   
        },
        copyN(str) {
            return new Promise(function(resolve, reject) {
                if (!window.cordova) {
                    return reject();
                }
                cordova.exec(resolve, reject, 'nativeJs', 'copy', [str]);
            })
        },
        goHomeN() {
            return new Promise(function(resolve, reject) {
                if (!window.cordova) {
                    return reject();
                }
                cordova.exec(resolve, reject, 'nativeJs', 'goHome', []);
            })
        },
        defBackgorund(index) {
            var Arr = ['rgb(248,243,235)', 'rgb(227,208,205)', 'rgb(212,220,217)', 'rgb(232,212,209)', 'rgb(233,230,210)', 'rgb(211,210,219)'];
            if(index){
                var indexNow = (index + 1) % Arr.length;
            }else{
                var indexNow = Math.floor((Math.random() * Arr.length));
            }
            return Arr[indexNow];
        },
        payN({ type, data }) {
            return new Promise(function(resolve, reject) {
                if (!window.cordova) {
                    return reject();
                }
                cordova.exec(resolve, reject, 'nativeJs', 'pay', [{ type, data }]);
            })
        },
        shareN(shares) {
            return new Promise(function(resolve, reject) {
                if (!window.cordova) {
                    return reject();
                }
                cordova.exec(resolve, reject, 'nativeJs', 'share', [shares]);
            })
        },
        isNotNull(val) {
            return val !== undefined && val !== null
        },
        prviewImage(e) {
            return new Promise(function(resolve, reject) {
                let file = e.target.files[0];
                let size = (file.size / 1024 / 1024).toFixed(2);
                if (!/image\/\w+/.test(file.type)) {
                    return reject('请选择图片文件');
                }
                if (size > 4) {
                    return reject('文件过大,不超过4MB');
                };
                let reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function() {
                    return resolve(this.result)
                }
            })
        },
        setStorage(key,model){
            return new Promise(function (resolve, reject) {

                if (!model || !(model instanceof Array)) return reject();

                model = JSON.stringify(model);

                localStorage.setItem(key, model);
                
                resolve();
            });
        },
        getStorage(key) {
            return new Promise(function (resolve, reject) {

                var json = localStorage.getItem(key);
                  
                if(!json){
                    
                    return reject();
                } 

                resolve(JSON.parse(json));
  
            });
        }        
    }
}


